* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}

::placeholder {
  color: red;
}

@font-face {
  font-family: 'AlexB';
  src: url("./../fonts/alexbrush-regular-webfont.woff2") format("woff2"), url("./../fonts/alexbrush-regular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

body {
  background: url("./../images/logo.png") center center no-repeat #666;
  height: 100vh;
  font-family: AlexB, sans-serif;
  font-size: 32px;
}




